body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  background-color: #93e646;
}

.outer_eye {
  position: absolute;
  background-color: white;
  width: 320px;
  height: 320px;
  border-radius: 50%;
  left: calc(50% - 160px);
  bottom: 40%;
  border: 5px solid black;
}

.inner_eye {
  position: absolute;
  z-index: 10;
  background-image: radial-gradient(black 0%, black 40%, #23a187 40%, #23a187 100%);
  width: 150px;
  height: 150px;
  border-radius: 50%;
  left: calc(50% - 75px);
  top: calc(50% - 75px);
  border: 5px solid black;
}
.inner_eye::before {
  content: "";
  position: absolute;
  z-index: 15;
  background-color: white;
  width: 30px;
  height: 26px;
  border-radius: 50%;
  top: 32%;
  left: 51%;
}

.smile {
  position: absolute;
  border-bottom: 15px solid black;
  border-radius: 50%;
  width: 80%;
  height: 57%;
}
.smile::before {
  content: "";
  position: absolute;
  border-top: 5px solid black;
  border-radius: 50%;
  width: 60px;
  height: 30px;
  left: calc(0% - 30px);
  bottom: calc(45% - 15px);
  transform: rotate(-10deg);
}
.smile::after {
  content: "";
  position: absolute;
  border-top: 5px solid black;
  border-radius: 50%;
  width: 60px;
  height: 30px;
  right: calc(0% - 30px);
  bottom: calc(45% - 15px);
  transform: rotate(10deg);
}